<template>
  <div style="width:300px; margin: auto;">
    <b-form-input v-model="username" placeholder="username" id="input_text"></b-form-input>
    <b-form-input v-model="password" placeholder="password" id="input_text"></b-form-input>
    <b-button v-on:click="signIn" variant="primary">Registration</b-button>
    <br>
    <Alert v-bind:text="text" ref="Alert" style="margin: auto;"/>
  </div>
</template>

<script>
import {reskip} from './../functions/reskip.js'
import {USERNAME_EXISTS, REGISTER, LOGIN} from '../constants/graphgl.js'
import Alert from './my_lib/Alert.vue'

export default {
  name: 'Registration',
  components: {
    Alert,
  },
  data: function() {
    return {

      username: '',
      password: '',

      text: '',
    }
  },
  apollo: {

    login: {
      query: LOGIN,
      skip: true,
    },
    register: {
      query: REGISTER,      
      skip: true,
    },
    usernameExists: {
      query: USERNAME_EXISTS,
      skip: true,
    }
  },
  methods: {
    async signIn(){
      const username = this.username
      const password = this.password

      reskip(this.$apollo.queries.register, {username, password})

      try{
        const resultRegister = await this.$apollo.queries.register.refetch({username, password})
        console.log(resultRegister.data.user)
        this.text = `${resultRegister.data.user.username}, congratulations! `
      }catch(e){
        this.text = `${e.message}`
      }
    
      this.$refs.Alert.showAlert()
    },
  },
}
</script>